body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar-outline {
    outline: 2px solid blue;
    padding: 0 20px;
    margin: 10px;
}

.list {
    display: flex;
    flex-direction: column;
}

.ticker-button {
    color: #5bc2c2;
    border-color: #5bc2c2
}

.ticker-button:hover {
    color: #212529;
    background-color: #52bebe;
    border-color: #8ad3d3
}

.ticker-button:focus,
.ticker-button.focus {
    box-shadow: 0 0 0 .2rem rgba(91, 194, 194, 0.5)
}

.ticker-button:not(:disabled):not(.disabled):active,
.ticker-button:not(:disabled):not(.disabled).active,
.show>.ticker-button.dropdown-toggle {
    color: #212529;
    background-color: #9cdada;
}
.search {
    outline: 2px solid green;
    padding: 10px;
    margin: 10px;
}

.error {
    color: red;
}
.chart {
    outline: 2px solid red;
    padding: 10px;
    margin: 10px;
}
.container {
    display: flex;
    flex-direction: column;
    margin: 10px;
    outline: 2px solid black;
}

.second-row {
    display: flex;
    flex-direction: row;
}

.normalize {
    margin: 10px;
    outline: 2px solid fuchsia;
    width: 120px;
}

.date-range {
    display: flex;
    flex-direction: row;
    outline: 2px solid lime;
    padding: 10px;
    margin: 10px;
}

